import React, { useState, useEffect, useMemo } from 'react';
import { 
  Box, 
  Container, 
  Typography, 
  FormGroup, 
  FormControlLabel, 
  Checkbox,
  Paper,
  Grid 
} from '@mui/material';
import * as d3 from 'd3';

import PizzaSales from './PizzaSales';

const VenteHoraires = () => {
  const [selectedPizzas, setSelectedPizzas] = useState({});
  const [data, setData] = useState([]);

  // Dfinition des dimensions du graphique
  const margin = { top: 20, right: 120, bottom: 30, left: 60 };
  const width = 960 - margin.left - margin.right;
  const height = 500 - margin.top - margin.bottom;

  // Dfinition des chelles
  const x = useMemo(() => 
    d3.scaleLinear()
      .domain([1, 12])
      .range([0, width]),
    [width]
  );

  const y = useMemo(() => 
    d3.scaleLinear()
      .domain([0, d3.max(data, d => d.sales)])
      .range([height, 0]),
    [height, data]
  );

  // Liste des types de pizzas
  const pizzaTypes = [
    'Margherita', 'Regina', 'Quatre Fromages', 'Vgtarienne',
    'Pepperoni', 'Hawaienne', 'Calzone', 'Napolitaine'
    // ... ajoutez les autres types
  ];

  // Filtrer les donnes en fonction des pizzas slectionnes
  const filteredData = useMemo(() => 
    data.filter(d => selectedPizzas[d.pizzaType]),
    [data, selectedPizzas]
  );

  // Configuration du graphique D3
  useEffect(() => {
    if (!data.length) return;

    const svg = d3.select("#chart")
      .append("svg")
      .attr("width", width + margin.left + margin.right)
      .attr("height", height + margin.top + margin.bottom)
      .append("g")
      .attr("transform", `translate(${margin.left},${margin.top})`);

    // Cration des lignes avec labels directs
    const line = d3.line()
      .x(d => x(d.month))
      .y(d => y(d.sales));

    // Ajout des labels  la fin des lignes
    svg.selectAll(".line-label")
      .data(filteredData)
      .enter()
      .append("text")
      .attr("class", "line-label")
      .attr("x", d => x(d[d.length - 1].month))
      .attr("y", d => y(d[d.length - 1].sales))
      .text(d => d[0].pizzaType);

    return () => {
      svg.selectAll("*").remove();
    };
  }, [data, selectedPizzas, x, y, width, height, margin, filteredData]);

  const handlePizzaToggle = (pizzaType) => {
    setSelectedPizzas(prev => ({
      ...prev,
      [pizzaType]: !prev[pizzaType]
    }));
  };

  return (
    <Container maxWidth="lg">
      <Box sx={{ my: 4 }}>
        <Typography variant="h4" gutterBottom>
          Ventes Mensuelles par Type de Pizza
        </Typography>
        
        <PizzaSales/>

        <Grid container spacing={2}>

          <Grid item xs={12} md={9}>
            <Paper elevation={2} sx={{ p: 2 }}>
              <Typography variant="h6" gutterBottom>
                Analyse des Tendances
              </Typography>
              <Typography paragraph>
                L'analyse des ventes mensuelles rvle des tendances saisonnires marques 
                dans la consommation de pizzas. Les pizzas lgres et vgtariennes connaissent 
                une hausse significative pendant les mois d't, tandis que les pizzas plus 
                garnies sont privilgies en hiver.
              </Typography>
              <Typography paragraph>
                On observe galement que certains types de pizzas maintiennent une popularit 
                constante tout au long de l'anne, notamment les classiques comme la Margherita 
                et la Regina, qui reprsentent une base stable de nos ventes.
              </Typography>
            </Paper>
          </Grid>
        </Grid>
      </Box>
    </Container>
  );
};

export default VenteHoraires;